// Framework
import * as React from "react";

// Functionality
import AsyncEvent from "common/Helper/AsyncEvent";

// Types
import { Services, ServiceNotification } from "./types";

export const ServiceUpdateEvent = new AsyncEvent<ServiceNotification>();

export const ServiceContext = React.createContext({} as Services);

export const ServiceContextProvider: React.FC = ({ children }) => {

	const [serviceState, setServiceState] = React.useState<Services>({} as Services);

	const serviceRef = React.useRef({} as Services);

	React.useEffect(() => {
		const listenerId = ServiceUpdateEvent.Register(notification => {
			serviceRef.current[notification.name] = notification.service as any;
			setServiceState({ ...serviceRef.current });
			return Promise.resolve();
		});

		return () => ServiceUpdateEvent.Unregister(listenerId);
	});

	return (
		<ServiceContext.Provider value={serviceState}>
			{ children}
		</ServiceContext.Provider>
	);
}

export default ServiceContext;